.our-offers-container{display:flex;justify-content:center;gap:20px;padding-top:4%;padding-bottom:8%;padding-left:6%;padding-right:6%}.image-main-container{position:relative}.image-container:hover{cursor:pointer}.image-offer{width:400px;cursor:pointer}.image-container::after{content:"";position:absolute;bottom:4px;width:0%;display:block;height:10px;background-color:#bf0f0f;transition:.3s}.image-container:hover::after{width:100%}.title-offer{font-size:24px;font-weight:400;padding-top:12px;color:var(--title-text-color)}.line-offer{background-color:#bf0f0f;height:2px;width:50px;margin-top:10px}@media (max-width:1330px){.our-offers-box-container{display:flex;flex-direction:column}.our-offers-container{flex-wrap:wrap}.image-offer{width:330px}}@media (max-width:600px){.our-offers-container{padding-left:12%;padding-right:12%;padding-top:12%}.image-offer{width:100%}}